* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}
body {
	font-family: Arial, Helvetica, sans-serif;
	line-height: 1.4;
}
a {
	color: #333;
	text-decoration: none;
}
button {
	float: right;
	background-color: #ff0000;
	border: none;
	color: #fff;
	padding: 5px 9px;
	border-radius: 50%;
}
.container {
	padding: 0px 1rem;
}
.btn {
	display: inline-block;
	border: none;
	background: #555;
	color: #fff;
	padding: 7px 20px;
	cursor: pointer;
}
.btn :hover {
	background: #666;
}
